      if (
        file.type ==
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" &&
        this.$route.params.id_tag
      ) {
        axios({
          method: "post",
          url: "/shell-thickness/upload-shell-thk-cml?id_tag=" + id_tag,
          headers: {
            "Content-Type": "multipart/form-data",
