              console.log("success");
              // this.FETCH_CML();
              // this.FETCH_LAST_INSP_THK();
            }
          })
          .catch(error => {
            this.$ons.notification.alert(
              error.code + " " + error.response.status + " " + error.message
            );
          })
          .finally(() => {
            this.isLoading = false;
            this.$ons.notification.alert("Upload Completed!");
          });
      } else {
