        })
        .finally(() => {
          this.isLoading = false;
          this.is_changed_dwg = 0;
        });
    },
    ON_ATTACHMENT_CHANGE(e) {
      console.log(e);
      this.isInitEdit = 1;
      let reader = new FileReader();
      reader.readAsDataURL(e.value[0]);
      reader.onload = () => {
        this.imgHolder = reader.result;
      };
      this.file = e.value[0];
    },
    EDITING_START_ADDITIONAL_REMARK(e) {
      console.warn("EDIT");
      console.log(e);
      this.file = [];
      this.isInitEdit = 0;
      this.file_path = e.data.file_path;
      this.dataIMGTemp = e;
    },
    SAVE(e) {
      console.log("save:");
      console.log(e);
      if (e.changes.length == 0) {
        this.UPDATE_ADDITIONAL_REMARK(this.dataIMGTemp);
      }
    },
    INIT_NEW_ROW_ADDITIONAL_REMARK() {
      this.imgHolder = "";
      this.file = [];
      this.isInitEdit = 1;
    },
    SHOW_HIDE_PANEL() {
      this.pagePanelHiding = !this.pagePanelHiding;
    },
    DATE_FORMAT(d) {
      if (d == null) {
        return "-";
      }
      return moment(d).format("DD MMM yyyy");
    },
    TO_FIXED(val, position) {
      if (val == null) {
        return 0;
      }
      return val.toFixed(position);
    }
  }
};
</script>

